<template>
<div class="seacrh">
  <div style="width: 100%;height: 28px;background-color: #FFF"></div>
  <form action="/">
    <van-search
      v-model="searchText"
      :placeholder="$t('ISearch')"
      show-action
      shape="round"
      @cancel="onCancel"
    >
    <!-- -------------zlq----------------------- -->
      <!-- <span slot="action">{{$t('cancel')}}</span> -->
    <!-- -------------zlq----------------------- -->
    </van-search>
  </form>
  <div class="hot">{{$t('Popular')}}</div>
  <div class="hotContent">
    <div class="hc-list" v-for="(item,index) in  hotTickList" :key="index" @click="toDatil2(item)"> <span class="hc-name" >{{$i18n.locale !== 'zh'?item.nameEN:item.name}}</span><span class="hc-num right" :class="[item.diffPrice>0?'changeRed':(item.diffPrice<0?'changeGre':'')]">{{item.diffPriceRate}}</span></div>
  </div>
  <div class="scroll" ref="searchBox">
    <div class="result" ref="result">
      <div class="show" v-for="(item2,index) in listShow" :key="index" @click="toDatil2(item2)">
        <span>{{item2.name}}</span><span>{{item2.instrumentid}}</span><span :class="[item2.diffPrice>0?'changeRed':(item2.diffPrice<0?'changeGre':'')]">{{item2.newPrice}}</span>
      </div>
    </div>
  </div>

  <div class="loading" v-show="!tickCopyOK">
    <van-loading  color="#FF7100" size="40px" class="loadingCon"></van-loading>
  </div>
</div>
</template>

<script>
  import Vue from 'vue';
  import { Search } from 'vant';
  import Bscroll from 'better-scroll'
  Vue.use(Search);
  export default {
    data(){
      return {
        searchText:'',
        list:[],
        listShow:[],
        tickCopyOK:false,
        hotList:{},
        hotTickList:[],
        digits:0
      }
    },
    props:{
      mainList:{
        type:Object
      },
      Tick:{

      },
      tickCopy:{
        type:Object
      }
    },
    created(){
      let timeVal = setInterval(()=>{
        if(this.mainList!==null&&localStorage.tickCopy){
          for(let i in this.tickCopy){
            let H = new RegExp('HSI');
            let C = new RegExp('CL');
            let M = new RegExp('MHI');
            let N = new RegExp('CN');
            if(H.test(i)){
              if(this.hotList['HSI']){
                let pre = ((new RegExp('[0-9]{1,9}')).exec(this.hotList['HSI']))[0];
                let next = ((new RegExp('[0-9]{1,9}')).exec(i))[0];
                if(Number(next)>Number(pre)){
                  this.$set(this.hotList,'HSI',i)
                }else {
                  continue;
                }
              }else {
                this.$set(this.hotList,'HSI',i)
              }
            }else if(C.test(i)){
              if(this.hotList['CL']){
                let pre = ((new RegExp('[0-9]{1,9}')).exec(this.hotList['CL']))[0];
                let next = ((new RegExp('[0-9]{1,9}')).exec(i))[0];
                if(Number(next)>Number(pre)){
                  this.$set(this.hotList,'CL',i)
                }else {
                  continue;
                }
              }else {
                this.$set(this.hotList,'CL',i)
              }
            }else if(M.test(i)){
              if(this.hotList['MHI']){
                let pre = ((new RegExp('[0-9]{1,9}')).exec(this.hotList['MHI']))[0];
                let next = ((new RegExp('[0-9]{1,9}')).exec(i))[0];
                if(Number(next)>Number(pre)){
                  this.$set(this.hotList,'MHI',i)
                }else {
                  continue;
                }
              }else {
                this.$set(this.hotList,'MHI',i)
              }
            }else if(N.test(i)){
              if(this.hotList['CN']){
                let pre = ((new RegExp('[0-9]{1,9}')).exec(this.hotList['CN']))[0];
                let next = ((new RegExp('[0-9]{1,9}')).exec(i))[0];
                if(Number(next)>Number(pre)){
                  this.$set(this.hotList,'CN',i)
                }else {
                  continue;
                }
              }else {
                this.$set(this.hotList,'CN',i)
              }
            }
          }
          this.tickCopyOK=true;
          this.hotTickList={};
          console.log('11111',this.hotList)
          // for(let tck in this.mainList){
          //   console.log(tck)
          //   for(let obj in this.hotList){
          //     // console.log(this.mainList[i].instrumentid)
          //     this.mainList[tck].instrumentid
          //     if(this.mainList[tck].instrumentid===this.hotList[obj]){
          //       // this.$set(this.hotTickList,this.hotTickList.length,this.mainList[tck])
          //     this.hotTickList[tck]=this.mainList[tck];
          //     }
          //   }
          // }
            for(let obj in this.hotList){
              let tck = this.hotList[obj]
              this.hotTickList[tck]=this.mainList[tck];
            }
        }
        console.log(this.hotTickList)
        window.clearInterval(timeVal)
      },100)
    },
    mounted(){
      // this.$nextTick(()=> {
      //   this.scroll = new Bscroll(this.$refs.searchBox, {
      //     click: true
      //   })
      // });
    },
    methods:{
      onCancel(){
        this.$router.go(-1)
      },
      toDatil2(item2){
        this.$router.push({
          name:'detail',
          params:{
            instrumentId:item2.instrumentid,
            name:item2.name,
            type:item2.type,
            id:'pay1',
            Fentime:item2.timeQuantum,
            openHandM:item2.openHandM,
            margin:item2.margin,
            item:item2,
            id2:'save2',
            digits:item2.digits
          }
        })
      }
    },
    watch:{
      searchText(newVal){
        if(newVal===''){
          this.listShow=[];
          this.$refs.searchBox.style.display='none';
        }else {
          this.$refs.searchBox.style.display='block';
          let str='';
          for(let i=0;i<newVal.length;i++){
            if(i===newVal.length-1){
              str+=newVal[i]
            }else {
              str+=newVal[i]+'.*'
            }
          }
          let list = {};
          let a =new RegExp(`${str}`);
          // for(let i=0;i<this.mainList.length;i++){
          for(let tck in this.mainList){
            let str = this.mainList[tck].name+this.mainList[tck].instrumentid;
            let reg = a.test(str);
            if(reg){
              // list.push(this.mainList[tck])
              list[tck]=this.mainList[tck];
            }
          }
          this.listShow=list;
        }
      },
      Tick(){
        this.hotTickList={};
        for(let obj in this.hotList){
          let tck = this.hotList[obj];
          this.hotTickList[tck]=this.mainList[tck];
        }
        if( this.tickCopyOK=true){
          return
        }
        if(this.mainList.length>0){
          this.tickCopyOK=true;
        }
      },

    }
  }
</script>

<style lang="scss" scoped>
  @import "../../style/theme2";
.seacrh {
  z-index: 2;
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  background-color: #FFF;
  padding:tovw(0) tovw(12) 0 tovw(20);
  .hot {
    font-size: tovw(14);
    padding-top: tovw(20);
  }
  .hotContent {
    padding-top: tovw(10);
    width: tovw(343);
    .hc-list {
      display: inline-block;
      width: 50%;
      text-align: left;
      .hc-name {
        display: inline-block;
        font-size: tovw(12);
        padding-bottom: tovw(10);
        line-height: tovw(12);
        height: tovw(12);
        padding-right: tovw(10);
      }
      .hc-num {
        display: inline-block;
        font-size: tovw(12);
        padding-bottom: tovw(10);
        line-height: tovw(12);
        height: tovw(12);
        &.changeRed {
          color:#FE0000;
        }
        &.changeGre {
          color:#25B100;
        }
      }
    }
  }
  .scroll {
    display: none;
    width: tovw(343);
    background-color: #FFF;
    padding-top: tovw(10);
    position: absolute;
    left: tovw(20);
    top:tovw(73);
    bottom: tovw(20);
    overflow: auto;
    .result {
      width: 100%;
      .show {
        width: 100%;
        height: tovw(41);
        font-size: tovw(14);
        padding: tovw(10) 0;
        line-height: tovw(20);
        border-bottom: tovw(1) solid #D5D3D3;
        text-align: left;
        &>span:nth-child(1){
          display: inline-block;
          width: tovw(100);
          font-size: tovw(14);
        }
        &>span:nth-child(2){
          display: inline-block;
          width: tovw(80);
          font-size: tovw(12);
          color:#8C8B8B;
        }
        &>span:nth-child(3){
          display: inline-block;
          width: tovw(100);
          font-size: tovw(13);
          color:#25B100;
          &.changeRed {
            color:#FE0000;
          }
          &.changeGre {
            color:#25B100;
          }
        }
      }
    }
  }
  .scroll::-webkit-scrollbar {
    display: none;
  }
  .loading {
    z-index: 5;
    width: 100%;
    position: absolute;
    top: tovw(54);
    bottom: tovw(0);
    background-color: rgba(225,225,225,.2);
    padding-top: 50%;
    text-align: center;
    .van-loading__text {
      margin-left: tovw(20);
    }
  }
}
</style>



